.systemsDiv{
    width:100%;
}

.systemsHeader{
    width: 100%;
    height:50px;

    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0px;
}

.systemsHeaderInput{
    width: 100px;
    /* font-family: "Verdana";
    font-size: 50px;
    font-weight: bold;
    color: #2D1A0D;  */
}

.systemsButtonText{
    font-size: 20px;
    font-weight: bold;
    color: #2D1A0D;
}

.systemsHeader > p{
    font-family: "Verdana";
    font-size: 20px;
    font-weight: bolder;
    color: #2D1A0D;
    margin:5px;
}

.systemsTable{
    border-collapse:separate;
    border-spacing:2px 0px;
}

.tableRow{
    height: 35px;
    width:100%;
    margin-top:2px;
    margin-bottom:2px;
}

.blue{
    background-color:lightcyan;
}
.gray{
    background-color:lightgray;
}

.red{
    background-color:lightcoral;
}

.tableElement {
    margin-left:2px;
    margin-right:2px;
}


.tableText {
    font-family: "Verdana";
    font-size: 20px;
    color: #2D1A0D;
    margin:5px;
}

.boldText{
    font-weight: bold;
}
.actionButton{
    width:100%;
    height:45px;
}    


.actionButtonText{
    font-family: "Verdana";
    font-size: 20px;
    color: white;
    margin:5px;
    margin-top:auto;
    margin-bottom:auto;
}