.menuElement {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width:100%;
    margin-top: 2px;
    margin-bottom: 2px;
}

.menuElementText {
    font-family: "Verdana";
    font-weight: bold;
    font-size: 20px;
    color: #2D1A0D;
    margin: 0px;
}

.menuTitle {
    font-family: "Verdana";
    font-weight: bold;
    font-size: 25px;
    color: white;
    text-align: center;
    width:100%;
    padding-bottom: 5px;
    margin:1px;
    border-bottom: 2px white solid;
}

