.tableRow{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width:100%;
    margin-top:2px;
    margin-bottom:2px;
}

.blue{
    background-color:lightcyan;
}
.gray{
    background-color:lightgray;
}

.red{
    background-color:lightcoral;
}

.tableRow > p {
    font-family: "Verdana";
    font-size: 20px;
    color: #2D1A0D;
    margin:5px;
}

.idx{
    width:50px;
}

.name{
    width:200px;
}

.uuid{
    width:500px;
}

.services{
    width:720px;
}

.admin{
    width:150px;
}